.event_list {
  border: 1px solid #494440;
  width: 100%;
  margin: 0 auto;
  padding: 17px 9px 13px;
  margin-bottom: 30px;
  color: $color-text01;
  text-decoration: none;
  position: relative;
  &__inner {
    width: 100%;
    display: flex;
    .photo {
      flex: 0 0 28.575%;
      height: 18.75vw;
      margin: 7px 10px 0 0;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        font-family: 'object-fit: contain;';
      }
    }
    .overview {
      flex: 1 1 auto;
      margin: 0;
      padding: 0;
      position: relative;
    }
  }
  .memo {
    @include font-line(12, 12);
    margin: 15px 0;
  }
  .title {
    font-weight: 700;
    @include font-line(14, 14);
    margin: 0 0 10px;
  }
  .more {
    width: 101px;
    margin: 0 0 0 auto;
    border: 1px solid #322e2c;
    color: $color-text01;
  }
}

/*詳細ページ*/
.detail {
  @include font-line(12, 18);
  margin: 0;
  &__heading {
    margin: 0 0 20px;
    @include font-line(15, 18);
    padding-bottom: 5px;
  }
  .return {
    width: 101px;
    margin: 0 0 0 auto;
    .backButton {
      border: 1px solid #322e2c;
      color: $color-text01;
      i {
        padding-right: 5px;
      }
    }
  }
  .p-photoList {
    a {
      border:none;
      padding: 0;
      text-decoration: none;
    }
    &__title {
      margin: 5px 0 15px;
      color: $color-text01;
      text-align: center;
    }
  }
  div {
    &.img {
      width: 100%;
      margin: 0 auto 10px;
      padding: 0;
      img {
        width: 100%;
        height: auto;
      }
      a {
        border: none;
        padding: 0;
        .zoomIcon {
          display: none;
        }
      }
      .memo {
        margin: 10px 0 0;
        @include font-line(12, 14);
        text-align: center;
      }
    }
    &.detail01 {
      margin: 0;
      padding: 0 5px;
      position: relative;
      .mapButton {
        width: 145px;
        margin: 10px 0 0 auto;
      }
    }
  }
  .detail02 {
    margin-top: 15px;
    margin-bottom: 15px;
    font-weight: 500;
    &__heading {
      margin: 0 0 8px;
    }
    &__text {
      margin: 0;
      @include font-line(12, 18);
    }
  }
}
